* {
	font-family: "Mulish", sans-serif;
}

html ::-webkit-scrollbar {
	display: none;
}

body {
	overflow-x: hidden;
	z-index: 0;
	margin: 0;
}

.my-container {
	max-width: 1500px;
}

.lead,
.text-primary {
	text-align: center;
}

.fa-youtube {
	color: #e52d27;
	transition: 300ms;
}
.fa-twitter {
	color: #55acee;
	transition: 300ms;
}
.fa-facebook {
	color: #3b5998;
	transition: 300ms;
}
.fa-linkedin {
	color: #0976b4;
	transition: 300ms;
}

.fa-youtube:hover {
	color: rgb(189, 33, 33);
}
.fa-facebook:hover {
	color: #214968;
}
.fa-linkedin:hover {
	color: #095b8b;
}
.fa-twitter:hover {
	color: #318fd8;
}

.fa-instagram {
	color: transparent;
	background: radial-gradient(
		circle at 30% 107%,
		#fdf497 0%,
		#fdf497 5%,
		#fd5949 45%,
		#d6249f 60%,
		#285aeb 90%
	);
	transition: 300ms;
	background: -webkit-radial-gradient(
		circle at 30% 107%,
		#fdf497 0%,
		#fdf497 5%,
		#fd5949 45%,
		#d6249f 60%,
		#285aeb 90%
	);
	background-clip: text;
	-webkit-background-clip: text;
}

.navbar {
	padding: 2em 3em;
	border-bottom: 2px solid #eeeeee;
}
.nav-links {
	display: flex;
	flex-direction: row;
	align-content: space-between;
	align-items: center;
	list-style: none;
}

@media only screen and (max-width: 1135px) {
	.nav-links {
		display: none;
	}
}
@media only screen and (min-width: 1135px) {
	.hamburger-icon {
		display: none;
	}
	.side-panel {
		display: none;
	}
	.side-panel-links {
		display: none;
	}
}

ul {
	list-style: none;
	margin-bottom: 0;
	padding: 0;
}

.navbar-link {
	text-transform: uppercase;
	color: #020101;
	font-size: 0.8rem;
	letter-spacing: 0.15rem;
	font-weight: 600;
	margin: 0 0.8em 0 0.4em;
	transition: 300ms;
}

.navbar-link:hover {
	text-decoration: none;
	color: #008cc9;
}
.navbar > .logo {
	font-size: 1.25rem;
}

.landing {
	background: url("background.jpeg") no-repeat;
	height: 88vh;
	background-size: cover;
}

.dark-overlay {
	background-color: rgba(0, 0, 0, 0.5);
	position: relative;
	left: 0;
	width: 100%;
	height: 100%;
}

.landing-inner {
	color: #fff;
	height: 100%;
	width: 80%;
	margin: auto;
	display: flex;
	letter-spacing: 0.2rem;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
}
.landing-inner > p {
	max-width: 800px;
}
.auth-buttons {
	margin: 2em;
}

.auth-buttons > a {
	width: 100px;
	margin: 1em;
	text-align: center;
}

.btn {
	letter-spacing: 0.05em;
	padding-top: 0.8em;
	padding-bottom: 0.8em;
}

.alerts-container {
	display: flex;
	align-items: center;
	justify-content: center;
	justify-items: center;
}

.alert {
	max-width: 500px;
	width: 100%;
	position: absolute;
	text-align: center;
	box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
	animation: 0.4s ease-out forwards animatetop, 0.4s ease-out 1.8s animatedown;
	z-index: 999;
}

.alert,
.alert-danger {
	background-color: #d40000;
	color: white;
}

.alert-safe {
	background-color: rgb(73, 195, 73);
}

@-webkit-keyframes animatetop {
	from {
		top: -150px;
	}
	to {
		top: 150px;
	}
}

@keyframes animatetop {
	from {
		top: -150px;
	}
	to {
		top: 150px;
	}
}

@-webkit-keyframes animatedown {
	from {
		top: 150px;
	}
	to {
		top: -150px;
	}
}

@keyframes animatedown {
	from {
		top: 150px;
	}
	to {
		top: -150px;
	}
}

.form-container {
	margin: 5em auto;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.form {
	border: 2px solid #eeeeee;
	padding: 3em 2em;
	border-radius: 10px;
	width: 100%;
}

.auth-form {
	max-width: 600px;
}

.x-large,
.lead {
	text-transform: uppercase;
}

input,
.form-dropdown {
	outline: none;
	padding: 0.5em;
	width: 100%;
	border: 1.5px solid #ccc;
	border-radius: 4px;
}
.profile-form-container,
.post-form-container {
	margin: 2em auto;
}
.post-from {
	width: 100%;
}
/* .post-form-container{
	margin: 2em 0;
	text-align: center;
} */
.form-header {
	text-align: center;
}

.social-input {
	display: flex;
}

.fab {
	margin-top: 0.2em;
	margin-right: 0.5em;
}

select {
	padding: 0.5em;
	outline: none;
}

.back-submit-buttons {
	display: flex;
	justify-content: space-evenly;
}

.checkbox-inline {
	display: flex;
}

.checkbox-inline > input {
	margin-top: 0.5em;
}

.checkbox-inline > label {
	width: 100%;
}

thead,
tr {
	text-align: center;
}

tr > .btn {
	margin-top: 0.45em;
	padding-top: 0.2em;
	padding-bottom: 0.2em;
}

.profiles {
	display: grid;
	grid-column-gap: 50px;
	grid-template-columns: auto auto;
}

.profile-card {
	display: flex;
	border-radius: 5px;
	margin-bottom: 1em;
	margin-top: 0em;
}
.round-img {
	width: 100%;
	max-width: 120px;
	max-height: 120px;
	border-radius: 50%;
}

.profile-card-img {
	text-align: center;
	align-items: center;
	margin: auto auto;
}

.skills-list {
	list-style: none;
}

@media only screen and (max-width: 1200px) {
	.profiles {
		grid-template-columns: auto;
	}
}

.profile-grid {
	/* display: grid;
	grid-template-areas:
		"top top"
		"exp edu";
	grid-gap: 1rem; */
}

.profile-top {
	display: flex;
	flex-direction: row;
	width: 100%;
}

.profile-top img {
	width: 250px;
}

/* 
.profile-top .icons a {
	margin: 0 0.3rem;
} */

.fa-globe {
	margin-right: 0.7em;
}

.profile-about {
	grid-area: about;
	text-align: center;
}

.profile-about .skills {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	text-align: center;
}

.profile-exp {
	grid-area: exp;
}

.profile-edu {
	grid-area: edu;
}

.profile-exp h2,
.profile-edu h2 {
	margin-bottom: 1rem;
}

.profile-exp > div,
.profile-edu > div {
	margin: 1rem 0;
	padding: 1rem;
}

.profile-exp > div:last-child,
.profile-edu > div:last-child {
	border: 0;
}

.profile-exp p,
.profile-edu p {
	margin: 0.5rem 0;
}

.profile-grid > div {
	border: #ccc 1px solid;
}

.skills-list > .text-primary {
	text-align: left;
}

.head-area {
	padding: 2em 2em;
}

.experience-card,
.education-card,
.post-card {
	border: rgb(216, 216, 216) 1px solid;
	padding: 1em 1em;
	margin: 1em 2em;
	max-width: 1000px;
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px
}

.post-card {
	margin: auto;
}
.post-card-row {
	margin-left: 0;
	margin-right: 0;
}

.post-actions {
	display: flex;
	justify-content: space-around;
}

.post-date {
	margin-top: auto;
	margin-bottom: auto;
}

.post-action > i,
.post-action > span {
	padding: 0.25em;
	margin: 0.25em;
}



.post-action {
	display: flex;
	align-items: center;
	text-align: center;
	padding: 0.5em;
	justify-content: center;
}

.post-icons {
	width: 30px;
	height: 30px;
}

.single-post-text {
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 6;
	-webkit-box-orient: vertical;
}

.landing-inner > h1 {
	position: relative;
	margin: 0 auto;
	border-right: 2px solid rgba(255, 255, 255, 0.75);
	white-space: nowrap;
	overflow: hidden;
	animation: typewriter 1.5s steps(25) 1s 1 normal both,
		blinkTextCursor 700ms steps(25) infinite normal;
}

@keyframes typewriter {
	from {
		width: 0;
	}
	to {
		width: 455px;
	}
}
@keyframes blinkTextCursor {
	from {
		border-right-color: rgba(255, 255, 255, 0.75);
	}
	to {
		border-right-color: transparent;
	}
}

.lead-1,
.lead-2 {
	position: relative;
	margin: 0 auto;
	border-right: 2px solid rgba(255, 255, 255, 0.75);
	white-space: nowrap;
	overflow: hidden;
	transform: translateY(20%);
	animation: typewriter2 2.5s steps(25) 3s 1 normal both,
		blinkTextCursor 700ms steps(25) infinite normal;
}

.lead-2 {
	transform: translateY(40%);
	animation: typewriter3 2.5s steps(25) 5.5s 1 normal both,
		blinkTextCursor 700ms steps(25) infinite normal;
}

@keyframes typewriter2 {
	from {
		width: 0;
	}
	to {
		width: 600px;
	}
}

@keyframes typewriter3 {
	from {
		width: 0;
	}
	to {
		width: 435px;
	}
}

.side-panel {
	box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
	top: 0;
	bottom: 0;
	right: 0;
	/* left: 68%; */
	z-index: 999;
	background: #fff;
	position: absolute;
	width: 300px;
	height: 101vh;
	animation: ease-out 0.3s animateSidePanel;
}

.side-panel-links {
	display: flex;
	flex-direction: column;
	height: 20%;
	justify-items: space-between;
	animation: ease-in 0.3s animateSidePanel;
}

.side-panel-links > li {
	padding: 0.5em 0 0.5em 0;
	margin: auto auto;
}

@keyframes animateSidePanel {
	from {
		left: 920px;
		width: 0;
	}
}

.post-text-area {
	padding: 2em;
	min-height: 400px;
	flex-direction: column;
}

.heading-area {
	padding: 2em;
	align-items: center;
	border-bottom: 1px solid lightgrey;
}

.add-comment {
	width: 100%;
	display: flex;
	flex-direction: row;
}

.input-comment {
	width: 100%;
	border: 1px solid grey;
	border-radius: 50px 50px;
	padding-left: 1.2em;
	padding-right: 3em;
}

.comment-submit-button {
	position: relative;
	cursor: pointer;
	padding: 0;
	left: 95%;
	bottom: 2.2em;
	opacity: 0.6;
}

.delete-comment-button {
	cursor: pointer
}

.comments-section {
	display: flex;
	flex-direction: column;
}

.add-comment > input:active,
.add-comment > input:focus {
	border: 2px solid #1987e0;
}

.avatar-sm {
	width: 50px;
	height: 50px;
}

.single-comment-display {
	border-bottom: 1px solid lightgrey;
	padding: 1em 1em;
}

.hamburger-icon,
.nav-close-icon {
	cursor: pointer;
}

.admin-side-panel-subitem {
	font-weight: 500;
	background-color: rgba(243, 244, 246);
	box-sizing: border-box;
	align-items: center;
	color: rgba(55, 65, 81);
	cursor: pointer;
	display: flex;
	justify-content: space-around;
	padding: 2rem 4rem;
	border-left: 2px solid rgba(243, 244, 246);
	border-right: 3px solid rgba(243, 244, 246);
}

.selected-tab {
	font-weight: 500;
	box-sizing: border-box;
	background-color: #fff;
	align-items: center;
	color: rgba(55, 65, 81);
	cursor: pointer;
	display: flex;
	justify-content: space-around;
	padding: 2rem 4rem;
	
	border-left: 2px solid rgba(236, 72, 153);
	border-right: 3px solid rgba(243, 244, 246);
}

.admin-side-panel-subitem:hover {
	cursor: pointer;
	background-color: #fff;
	border-left: 2px solid rgba(236, 72, 153);
}

.panel-subsubitem {
	padding-left: 8em;
}

.admin-side-nav {
	background-color: rgba(243, 244, 246);
	min-height: 91vh;
	padding-left: 0;
	padding-right: 0;
}

.request-list-admin-dash {
	margin: auto;
	margin-top: 20px;
	height: 700px;
	overflow-y: scroll;

}

.float-child {
	float: left;
}

.join-request-card {
	border: 0.5px solid rgb(240, 238, 238);
	padding: 2em;
	margin: 1.5em 0 1.5em 0;
	display: flex;
	justify-content: space-between;
	border-radius: 5px;
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
	min-width: 600px;
}

.action-buttons-request-card {
	justify-content: space-between;
}

.action-button-request-card {
	border-radius: 4px;
	box-shadow: rgba(0, 0, 0, 0.1) 0 2px 4px 0;
	box-sizing: border-box;
	color: #fff;
	cursor: pointer;
	font-family: "Akzidenz Grotesk BQ Medium", -apple-system, BlinkMacSystemFont,
		sans-serif;
	font-size: 16px;
	font-weight: 400;
	outline: none;
	outline: 0;
	min-width: 10em;
	padding: 10px 25px;
	text-align: center;
	transform: translateY(0);
	margin: 1em 0 1em 0;
	transition: transform 150ms, box-shadow 150ms;
	user-select: none;
	-webkit-user-select: none;
	touch-action: manipulation;
	display: flex;
	justify-content: space-evenly;
}

.action-button-request-card:hover {
	box-shadow: rgba(0, 0, 0, 0.15) 0 3px 9px 0;
}

.approve-button {
	background-color: #13aa52;
	border: 1px solid #13aa52;
}

.reject-button {
	background-color: #eb4a4a;
	border: 1px solid #eb4a4a;
}

.no-data-page {
	text-align: center;
	font-size: xx-large;
	font-weight: 500;
	min-height: 800px;
}

.post-request-card {
	border: 0.5px solid rgb(240, 238, 238);
	padding: 2em;
	margin: 1.5em 0 1.5em 0;
	display: flex;
	justify-content: space-between;
	border-radius: 5px;
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
	min-width: 50em;
}

.post-request-card-user-info {
	padding-bottom: 0.5em;
	display: flex;
	justify-content: space-between;
	width: 100%;
}

.action-buttons-div {
	display: flex;
}
.float-left {
	width: 7em;
	display: flex;
	justify-content: left;
}

.float-right {
	float: right;
}

.parsed-text {
	border: 2px solid #eeeeee;
	padding: 1em;
	border-radius: 5px;
	width: 100%;
}

.ck-editor__editable {
	min-height: 300px;
}

