textarea {
	border-radius: 5px;
	padding: 0.8em;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;

	width: 100%;
}
textarea:focus {
	outline: none;
}

.tag {
	text-decoration: none;
	background: #e3f4ff;
	color: rgb(3 102 214);
	margin-top: 0.4em;
	margin-right: 1em;
	display: inline-block;
	font-size: 18px;
	font-weight: 500;
	border: 1px solid transparent;
	border-radius: 2em;
	padding: 0.2em 0.8em;
	line-height: 22px;
	transition: 300ms;
}

.tag:hover {
	text-decoration: none;
	background: #caf4ff;
}

.search-div,
.search-form {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.search-div {
	margin: 2em 5em 2em 2em;
}

.search-input {
	width: 100%;
	border: 1px solid grey;
	border-radius: 5px 5px;
	padding-left: 1.2em;
	padding-right: 3em;
	height: 55px;
}

.posts-top-item {
	margin-left: 0.5em;
	margin-right: 0.5em;
}

.search-form {
	padding-left: 0px;
	padding-right: 0px;
}

.contact-info,
.phone-icon,
.mail-icon,
.location-icon,
.footer-section {
	float: left;
	display: inline;
	width: 80%;
}

.phone-icon,
.mail-icon,
.location-icon {
	width: 15%;
	margin-top: 5px;
}

@media only screen and (max-width: 1135px) {
	.help-page {
		flex-direction: column;
	}
}

.footer-section {
	width: 49%;
	border-top: 2px solid #eeeeee;
	display: border-box;
}

.contact-info-text {
	margin-bottom: 0;
	margin-block-end: 0;
}

