.m-t-5 {
	margin-top: 5px;
}
.card {
	background: #eee;
	transition: 0.5s;
	border: 0;
	border-radius: 0.1875rem;
	display: inline-block;
	position: relative;
	width: 100%;
	box-shadow: none;
	height: 100%;
	padding: 0.5em;
}
.card .body {
	font-size: 14px;
	color: #424242;
	font-weight: 400;
}
.profile-page .profile-header {
	position: relative;
}

.profile-page .profile-header .profile-image img {
	border-radius: 50%;
	width: 140px;
	border: 3px solid #fff;
	box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.profile-page .profile-header .social-icon a {
	margin: 0 5px;
}

.profile-page .profile-sub-header {
	min-height: 60px;
	width: 100%;
}

.profile-page .profile-sub-header ul.box-list {
	display: inline-table;
	table-layout: fixed;
	width: 100%;
	background: #eee;
}

.profile-page .profile-sub-header ul.box-list li {
	border-right: 1px solid #e0e0e0;
	display: table-cell;
	list-style: none;
}

.profile-page .profile-sub-header ul.box-list li:last-child {
	border-right: none;
}

.profile-page .profile-sub-header ul.box-list li a {
	display: block;
	padding: 15px 0;
	color: #424242;
}

.user-role-tag {
	background-color: rgb(178 12 12);
	color: #fff;
	display: inline-block;
	padding-left: 8px;
	padding-right: 8px;
	text-align: center;
}

.achievement-card {
	width: 50em;
}

.footer-class {
	border-top: 2px solid #eeeeee;
}

.time {
	font-size: 9px !important;
}

.socials i {
	margin-right: 14px;
	font-size: 17px;
	color: #d2c8c8;
	cursor: pointer;
}

.feed-image img {
	width: 100%;
	height: auto;
}

.grid-container {
	display: grid;
	grid-template-columns: auto auto;
	column-gap: 20px;
	row-gap: 50px;
	padding: 2px;
	margin: auto auto;
}

.grid-item {
	border: 1px solid rgb(216, 212, 212);
	/* padding: 20px; */
	font-size: 30px;
	text-align: center;
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 16px;
}

.bg-grey {
	background: #eee;
}

.profile-tag {
	background: rgb(178 12 12);
	border-radius: 3px 0 0 3px;
	color: white;
	display: inline-block;
	height: 26px;
	line-height: 26px;
	padding: 0 20px 0 23px;
	position: relative;
	margin: auto;
	margin-top: 10px;
	text-decoration: none;
	width: 100px;
	padding-left: 2px;
	padding-right: 2px;
}

.profile-stats {
    margin-top: 2.3rem;
}

.profile-stats li {
    display: inline-block;
    font-size: 1.2rem;
    line-height: 1.5;
    margin-right: 4rem;
    cursor: pointer;
}

.profile-stats li:first-of-type {
    margin-left: 0;
}

.profile-stats li:last-of-type {
    margin-right: 0;
}

