.profile-buttons {
	margin-top: 2em;
	margin-bottom: 2em;
}
.profile-edit-btn {
	font-size: 1rem;
    line-height: 1.8;
    border: 0.1rem solid #dbdbdb;
    border-radius: 0.3rem;
    padding: 0 1.5rem;
    margin-left: 1rem;
	margin-right: 1rem;
	background: white;
}

.profile-edit-btn:hover {
	background: lightgray;
}

.user-type-count {
  background-color: #e1ecf4;
  border-radius: 10px;
  border: 1px solid #7aa7c7;
  box-shadow: rgba(255, 255, 255, .7) 0 1px 0 0 inset;
  box-sizing: border-box;
  color: #39739d;
  cursor: pointer;
  display: inline-block;
  font-family: -apple-system,system-ui,"Segoe UI","Liberation Sans",sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.15385;
  margin: 0;
  outline: none;
  padding: 8px .8em;
  position: relative;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  white-space: nowrap;
}

.user-type-count:hover,
.user-type-count:focus {
  background-color: #b3d3ea;
  color: #2c5777;
}

.user-type-count:focus {
  box-shadow: 0 0 0 4px rgba(0, 149, 255, .15);
}

.user-type-count:active {
  background-color: #a0c7e4;
  box-shadow: none;
  color: #2c5777;
}

.award-actions li {
    display: inline-block;
    margin-right: 1rem;
}

.award-actions li:first-of-type {
    margin-left: 0;
}

.award-actions li:last-of-type {
    margin-right: 0;
}



